import React from 'react'
import Title_ja from '../component/Title_ja';
import Search_ja from '../component/Search_ja';
import Language from '../component/Language';
import '../component/css/reset.css';
import './css/home.css';
import './css/home-mobile.css';
import { Link } from 'react-router-dom';

function Home_ja() {
  return (
    <div className='home'>
      <Language />
      <Title_ja className="Title"></Title_ja>
      <Search_ja className="Search"></Search_ja>
      <div className='list-box-wrap'>
        <ul className='list-box'>
          <li>
            <Link to={`/ja/${slugify("VIRTUAL SINGER")}`}>
              VIRTUAL SINGER
            </Link>
          </li>
          <li>
            <Link to={`/ja/${slugify("Leo/need")}`}>
              Leo/need
            </Link>
          </li>
          <li>
            <Link to={`/ja/${slugify("MORE MORE JUMP!")}`}>
              MORE MORE JUMP!
            </Link>
          </li>
          <li>
            <Link to={`/ja/${slugify("Vivid BAD SQUAD")}`}>
              Vivid BAD SQUAD
            </Link>
          </li>
          <li>
            <Link to={`/ja/${slugify("Wonderlands×Showtime")}`}>
              ワンダーランズ×ショウタイム
            </Link>
          </li>
          <li>
            <Link to={`/ja/${slugify("Nightcord at 25:00")}`}>
              25時、ナイトコードで。
            </Link>
          </li>
        </ul>
      </div>
    </div>
  )
}
function slugify(str) {
  if (typeof str !== 'string') return '';
  return str
    .toLowerCase()
    .normalize('NFD') // 유니코드 정규화 (선택)
    .replace(/[^\p{L}\p{N}\s-]/gu, '')  // 유니코드 기준: 글자, 숫자, 공백, 하이픈만 허용 (한글 포함)
    .replace(/\s+/g, '-')               // 공백 → 하이픈
    .trim();
}

export default Home_ja