import { useParams, Link } from 'react-router-dom';
import './css/characterpage.css';
import Search from '../component/Search';
import Countdown from '../component/Countdown';
import Language from '../component/Language'
import '../component/css/search-mobile.css';
import './css/characterpage-mobile.css';

function CharacterPage({ items }) {
    const { slug } = useParams();
    const normalizedSlug = slugify(slug);

    const unitMembers = items.filter((item) => {
        const group = item.groupId || {};
        const slugs = [
            slugify(group.ko),
            slugify(group.en),
            slugify(group.ja)
        ];
        return slugs.includes(normalizedSlug);
    });

    const character = items.find((item) => {
        const slugs = [
            slugify(item.ko),
            slugify(item.en),
            slugify(item.ja)
        ];
        return slugs.includes(normalizedSlug);
    });

    const unitName = unitMembers.length > 0 ? unitMembers[0].groupId.ko : null;

    if (unitMembers.length > 0) {
        return (
                <div className='contents'>
                    <Language />
                    <Search className='Search-box'></Search>
                    <h1 style={{ color: character.color || '#ffffff' }}>{unitName}</h1>
                    <ul>
                        {unitMembers.map((member) => (
                            <li key={member.ko}>
                                <Link to={`/${slugify(member.en)}`}>
                                    <div className='imgbox' style={{ backgroundColor: member.color || '#ffffff' }}>
                                        {member.ko} 이미지 들어갈 자리
                                    </div>
                                    <div className='txt' style={{ color: member.color || '#ffffff' }}>{member.ko}</div>
                                </Link>
                            </li>
                        ))}
                    </ul>
                </div>
        );
    }

    if (character) {
        return (
            <div>
                <Language />
                <Search className='Search-box'></Search>
                <Countdown memberName={character.ko}></Countdown>
            </div>
        );
    }

    return <h1>페이지를 찾을 수 없습니다.</h1>;
}

function slugify(str) {
    if (typeof str !== 'string') return '';
    return str
        .toLowerCase()
        .normalize('NFD')
        .replace(/[^\p{L}\p{N}\s-]/gu, '')
        .replace(/\s+/g, '-')
        .trim();
}

export default CharacterPage;