import './css/reset.css';
import './css/search.css';
import './css/search-mobile.css';
import React, { useState } from 'react';
import {useNavigate} from 'react-router-dom';

function Search(props) {
    const [query, setQuery] = useState('');
    const navigate = useNavigate();

    const handleSearch = () => {
        //입력창에서 받아온 검색어의 앞뒤 공백을 제거한 값이 존재하면 실행
        if (query.trim()) {
            //검색결과 페이지로 이동
            navigate(`/search?q=${encodeURIComponent(query)}`);
        }
    };

    const EnterPush = txt => {
        //Enter를 누르면 handleSearch함수 실행
        if (txt.key === 'Enter') handleSearch();
    };

    return (
        <div className={`search-wrap ${props.className}`}>
            <input
            //검색 타입으로 설정
                type='search'
                //입력창에 입력한 글자를 query 변수로 저장
                value={query}
                //입력창에 글이 입력될때마다 즉시 setQuery값을 해당 글의 값으로 변경
                onChange={
                    txt => setQuery(txt.target.value)
                }
                //Enter를 누를때마다 실행
                onKeyDown={EnterPush}
                //기본 상태
                placeholder='원하는 유닛/맴버를 검색해보세요'
                className='search-table'
                />
                {/* 버튼 누르면 실행 */}
            <button onClick={handleSearch} className='search-btn'>
                검색
            </button>
        </div>
    )
}

export default Search
