import './css/reset.css';
import './css/title.css';
import './css/title-mobile.css';
import React from 'react'

function Title({className}) {
    return (
        <header className={className}>
            <h1>
                프로세카 생일 카운트다운 페이지
            </h1>
        </header>
    )
}

export default Title
