import { useParams, Link } from 'react-router-dom';
import './css/characterpage.css';
import Search_en from '../component/Search_en';
import Countdown_en from '../component/Countdown_en';
import Language from '../component/Language'
import '../component/css/search-mobile.css';
import './css/characterpage-mobile.css';

function CharacterPage_en({ items }) {
    const { slug } = useParams();
    const normalizedSlug = slugify(slug);

    const unitMembers = items.filter((item) => {
        const group = item.groupId || {};
        const slugs = [
            slugify(group.ko),
            slugify(group.en),
            slugify(group.ja)
        ];
        return slugs.includes(normalizedSlug);
    });

    const character = items.find((item) => {
        const slugs = [
            slugify(item.ko),
            slugify(item.en),
            slugify(item.ja)
        ];
        return slugs.includes(normalizedSlug);
    });

    const unitName = unitMembers.length > 0 ? unitMembers[0].groupId.en : null;

    if (unitMembers.length > 0) {
        return (
                <div className='contents'>
                    <Language />
                    <Search_en className='Search-box'></Search_en>
                    <h1 style={{ color: character.color || '#ffffff' }}>{unitName}</h1>
                    <ul>
                        {unitMembers.map((member) => (
                            <li key={member.en}>
                                <Link to={`/en/${slugify(member.en)}`}>
                                    <div className='imgbox' style={{ backgroundColor: member.color || '#ffffff' }}>
                                        {member.en}'s image is in place
                                    </div>
                                    <div className='txt' style={{ color: member.color || '#ffffff' }}>{member.en}</div>
                                </Link>
                            </li>
                        ))}
                    </ul>
                </div>
        );
    }

    if (character) {
        return (
            <div>
                <Language />
                <Search_en className='Search-box'></Search_en>
                <Countdown_en memberName={character.en}></Countdown_en>
            </div>
        );
    }

    return <h1>Page not found.</h1>;
}

function slugify(str) {
    if (typeof str !== 'string') return '';
    return str
        .toLowerCase()
        .normalize('NFD')
        .replace(/[^\p{L}\p{N}\s-]/gu, '')
        .replace(/\s+/g, '-')
        .trim();
}

export default CharacterPage_en;