import { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import axios from 'axios';
import Search_ja from '../component/Search_ja';
import Language from '../component/Language'
import '../component/css/reset.css';
import './css/searchresultpage.css';
import './css/searchresultpage-mobile.css';

const BASE_URL = 'https://birthdaycountdownserver.onrender.com';

function useQuery() {
    //url에서 search이후의 query부분을 가독성 좋게 분해
    return new URLSearchParams(useLocation().search);
}

function SearchResultPage_ja() {
    //query부분에서 q값을 추출 후 유효한 값이면 그 값을 출력, 아니면 공백 출력
    const query = useQuery().get('q') || '';
    console.log(query);
    const [results, setResults] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        //query값이 유효하지 않으면 반환
        if (!query) {
            setLoading(false);
            return;
        }
        //완전한 결과값이 나올때까지 기다리지 말고 이하 실행
        const fetchData = async () => {
            try {
                setLoading(true);
                // '/'앞에다가 API주소 입력
                //API에서 query데이터값을 요청 후 나오면 응답
                const response = await axios.get(`${BASE_URL}/search?q=${encodeURIComponent(query)}`);
                //응답된 값의 데이터를 setResult에 임시 저장
                setResults(response.data);
            } catch (error) {
                setError("検索結果の読み込みに失敗しました");
            } finally {
                setLoading(false);
            }
        };
        fetchData();
        //query값이 변화할때마다 반복실행
    }, [query]);


    return (
        <div className='resultpage-wrap'>
            <Language />
            <Search_ja className="Search"></Search_ja>
            <div className='resultpage'>
                <h2>
                    {query} 検索結果 {results.length}件あります
                </h2>

                {/* 로딩 */}
                {loading && <p>ローディング中···</p>}

                {/* 에러시 출력 */}
                {error && <p style={{ color: 'red' }}>{error}</p>}

                {/* 로딩 완료 후 에러가 없을시 */}
                {!loading && !error && (
                    results.length > 0 ? (
                        <ul>
                            {results.map((item, idx) => (
                                <li key={idx}>
                                    <Link to={`/ja/${slugify(item.en)}`} style={{color: item.color}}>
                                        <div style={{backgroundColor: item.color}}>
                                            {item.ja} イメージ挿入位置
                                        </div>
                                        {item.ja}
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p>
                            検索結果がありません
                        </p>
                    )
                )}
            </div >
        </div >
    );
}
function slugify(str) {
    return str
        .toLowerCase()
        .replace(/[^a-z0-9\s-]/g, '') // 특수문자 제거
        .replace(/\s+/g, '-')         // 공백 → 하이픈
        .trim();
}

export default SearchResultPage_ja;