import React from 'react'

export default function NotFound({ lang = "ko" }) {
    const messages = {
        ko: "페이지를 찾을 수 없습니다",
        ja: "ページが見つかりませんでした",
        en: "Page not found"
    }
    return (
    <div>
        <h1>
            404
        </h1>
        <p>
            {messages[lang]}
        </p>
    </div>
)

}
