import React, { useEffect, useState } from 'react';
import './css/reset.css';
import './css/countdown.css';
import './css/countdown-mobile.css';

function Countdown_en({ memberName }) {
    const [memberInfo, setMemberInfo] = useState(null);
    const [timeLeft, setTimeLeft] = useState(null);
    const [isBirthdayToday, setIsBirthdayToday] = useState(false); // 생일 여부 상태

    const calculateTimeLeft = (targetDate) => {
        const difference = +targetDate - +new Date();
        return difference > 0
            ? {
                days: Math.floor(difference / (1000 * 60 * 60 * 24)),
                hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
                minutes: Math.floor((difference / (1000 * 60)) % 60),
                seconds: Math.floor((difference / 1000) % 60),
            }
            : { days: 0, hours: 0, minutes: 0, seconds: 0 };
    };

    const getNextBirthday = (birthday) => {
        if (!birthday) return null;
        const today = new Date();
        const currentYear = today.getFullYear();
        const [month, day] = birthday.split('-').map(Number);

        const birthdayDate = new Date(currentYear, month - 1, day);
        if (today > birthdayDate) birthdayDate.setFullYear(currentYear + 1);
        return birthdayDate;
    };

    // 오늘이 생일인지 체크하는 함수
    const checkIsBirthdayToday = (birthday) => {
        if (!birthday) return false;
        const today = new Date();
        const [month, day] = birthday.split('-').map(Number);
        return (
            today.getMonth() === month - 1 &&
            today.getDate() === day
        );
    };

    useEffect(() => {
        fetch(`https://birthdaycountdownserver.onrender.com/search?q=${encodeURIComponent(memberName)}`)
            .then((res) => res.json())
            .then((data) => {
                if (!Array.isArray(data) || data.length === 0) {
                    console.error('검색 결과 없음:', data);
                    return;
                }

                const memberData = data[0];
                if (!memberData.birthday) {
                    console.error('멤버 데이터에 생일 정보가 없습니다:', memberData);
                    return;
                }

                setMemberInfo(memberData);

                // 생일 당일 여부 설정
                const birthdayToday = checkIsBirthdayToday(memberData.birthday);
                setIsBirthdayToday(birthdayToday);

                if (birthdayToday) return; // 생일이면 카운트다운 생략

                const nextBirthday = getNextBirthday(memberData.birthday);
                setTimeLeft(calculateTimeLeft(nextBirthday));

                const timer = setInterval(() => {
                    setTimeLeft(calculateTimeLeft(nextBirthday));
                }, 1000);

                return () => clearInterval(timer);
            })
            .catch((error) => console.error('Failed to retrieve data:', error));
    }, [memberName]);

    if (!memberInfo) return <div style={{margin: "auto", textAlign: "center"}}>Loading...</div>;

    // 생일 당일 축하 화면
    if (isBirthdayToday) {
        return (
            <div style={{
                padding: '2rem',
                textAlign: 'center',
                color: memberInfo.color || '#fff',
                backgroundColor: '#fff',
                borderRadius: '1rem'
            }} className='isbirthday'>
                <h2>{memberInfo.firstName} <br /> Happy Birthday!</h2>
                <p>Today is {memberInfo.en}'s Birthday!</p>
            </div>
        );
    }

    // 기본 카운트다운 화면
    if (!timeLeft) return <div className='loading' style={{margin: "auto", textAlign: "center"}}>Loading...</div>;

    return (
        <div className='countdown-wrap'>
            <h2 style={{ color: memberInfo.color }}>Until {memberInfo.en}'s birthday</h2>
            <div style={{ color: memberInfo.color }}>
                <div className='time'>
                    <span>
                        {timeLeft.days === 1
                        ? `${timeLeft.days} d,`
                        : `${timeLeft.days} d,`
                        }
                    </span>
                    <span>
                        {timeLeft.hours === 1
                        ? `${timeLeft.hours} h,`
                        : `${timeLeft.hours} h,`
                        }
                    </span>
                    <span>
                        {timeLeft.minutes === 1
                        ? `${timeLeft.minutes} min,`
                        : `${timeLeft.minutes} min,`
                        }
                    </span>
                    <span>
                        {timeLeft.seconds === 1
                        ? `${timeLeft.seconds} sec`
                        : `${timeLeft.seconds} sec`
                        }
                    </span>
                </div>
                <p>left!</p>
            </div>
        </div>
    );
}

export default Countdown_en;
