import React, { useState, useEffect, useRef } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLanguage } from '@fortawesome/free-solid-svg-icons/faLanguage'
import { useLocation, useNavigate } from 'react-router-dom';
import './css/reset.css'
import './css/language.css';

function Language() {
    const lang = document.documentElement.lang || "en";

    const labels = {
        ko: "언어변경",
        ja: "言語を変更",
        en: "Change language"
    }

    const [open, setOpen] = useState(false);
    const btnRef = useRef(null);
    const menuRef = useRef(null);

    const {pathname, search, hash} = useLocation();
    const navigate = useNavigate();

    //바깥쪽 클릭시 닫기
    useEffect(() => {
        function onDocClick(e) {
            if (!menuRef.current || !btnRef.current) return;
            if (
                !menuRef.current.contains(e.target) &&
                !btnRef.current.contains(e.target)
            ) setOpen(false)
        }
        document.addEventListener("mousedown", onDocClick);
        return () => document.removeEventListener("mousedown", onDocClick);
    }, [])

    function switchLocalePath(path, to) {
        const segs = path.split('/');
        const first = segs[1];
        const isLocale = first === 'ja' || first == 'en';

        if (to === 'ko') {
            return isLocale ? '/' + segs.slice(2).join('/') : pathname;
        } else {
            if (isLocale) {
                segs[1] = to;
                return segs.join('/') || `${to}`;
            } else {
                return `/${to}${path === '/' ? '' : path}`
            }
        }
    }

    function go(to) {
        const next = switchLocalePath(pathname, to) + (search || '') + (hash || '');
        setOpen(false);
        navigate(next);
    }

    return (
        <div className='language'>
            <ul className='language-select'>
                <li className='btn'>
                    <button
                        ref={btnRef}
                        className='menu-button'
                        onClick={() => setOpen(o => !o)}
                        aria-label={labels[lang]}>
                        <FontAwesomeIcon icon={faLanguage} /> <span className='under'>▼</span>
                    </button>
                </li>
                <li>
                    {open && (
                        <>
                        <div
                        className='overlay'
                        onClick={() => setOpen(false)} />
                        <ul className='select'
                            ref={menuRef}
                            role='menu'>
                            <li lang='ko'>
                                <button onClick={() => go('ko')}>한국어</button>
                            </li>
                            <li lang='ja'>
                                <button onClick={() => go('ja')}>日本語</button>
                            </li>
                            <li lang='en'>
                                <button onClick={() => go('en')}>English</button>
                            </li>
                        </ul>
                        </>
                    )}
                </li>
            </ul>
        </div>
    )
}

export default Language
