import React from 'react'
import Title from '../component/Title';
import Search from '../component/Search';
import Language from '../component/Language';
import '../component/css/reset.css';
import './css/home.css';
import './css/home-mobile.css';
import { Link } from 'react-router-dom';

function Home() {
  return (
    <div className='home'>
      <Language />
      <Title className="Title"></Title>
      <Search className="Search"></Search>
      <div className='list-box-wrap'>
        <ul className='list-box'>
          <li>
            <Link to={`/${slugify("VIRTUAL SINGER")}`}>
              VIRTUAL SINGER
            </Link>
          </li>
          <li>
            <Link to={`/${slugify("Leo/need")}`}>
              Leo/need
            </Link>
          </li>
          <li>
            <Link to={`/${slugify("MORE MORE JUMP!")}`}>
              MORE MORE JUMP!
            </Link>
          </li>
          <li>
            <Link to={`/${slugify("Vivid BAD SQUAD")}`}>
              Vivid BAD SQUAD
            </Link>
          </li>
          <li>
            <Link to={`/${slugify("Wonderlands×Showtime")}`}>
              원더랜드×쇼타임
            </Link>
          </li>
          <li>
            <Link to={`/${slugify("Nightcord at 25:00")}`}>
              25시, 나이트코드에서.
            </Link>
          </li>
        </ul>
      </div>
    </div>
  )
}
function slugify(str) {
  if (typeof str !== 'string') return '';
  return str
    .toLowerCase()
    .normalize('NFD') // 유니코드 정규화 (선택)
    .replace(/[^\p{L}\p{N}\s-]/gu, '')  // 유니코드 기준: 글자, 숫자, 공백, 하이픈만 허용 (한글 포함)
    .replace(/\s+/g, '-')               // 공백 → 하이픈
    .trim();
}

export default Home